<template>
  <div class="col-12 align-items-center">
    <div class="row align-items-center">
      <h4>
        Balance: {{ stakersRewardTokenBalance }} SVC &nbsp;
        <i
          title="Refresh SVC Balance"
          @click="loadRewardTokenBalance"
          class="ri-refresh-line"
        ></i>
      </h4>
      <br />
      <h4 v-on:updateStakingPowerEvent="loadStakerInfo">
        Staking power: {{ stakersPower }}⚡ &nbsp;
        <i
          title="Refresh SVC Balance"
          @click="loadStakerInfo"
          class="ri-refresh-line"
        ></i>
      </h4>
      <br />

      <template v-if="stakersPower > 0">
        <h4>
          Staked for {{ formatSeconds(stakerStakedForSeconds) }}. (Harvest and
          unstake reset the timer.)
        </h4>
        <br />
        <h4>Current payout level: {{ stakersPayoutPct }}%</h4>
        <br />
        <h4 v-if="stakerNextPayoutPctInSeconds > 0">
          Next payout level: {{ stakersNextPayoutPct }}% -- reached in
          {{ formatSeconds(stakerNextPayoutPctInSeconds) }}
        </h4>
        <h4 v-else>
          Congratulations! You reached the highest payout level. 👏
        </h4>
      </template>

      <br />
      <h6>Total staked power: {{ totalStakedPower }}⚡</h6>
      <h6>Base power: {{ baseStakingPower }}⚡</h6>
      <h6>
        Total SVC Emission: ~{{
          Math.round(womEmissionPerHour * earningsTime * 100) / 100
        }}
        SVC / {{ hoursInWords[earningsTime] }}
      </h6>
      <br />
      <div
        class="settings-menu"
        v-if="womEmissionPerHour && totalStakedPower && stakersPower"
      >
        <button
          class="btn btn-primary"
          type="button"
          id="collection-filter"
          data-bs-toggle="dropdown"
          aria-expanded="false"
          style="font-size: 14px"
        >
          Earning ~{{
            Math.round(stakerEarningRateX1k * earningsTime) / 1000
          }}
          SVC / {{ hoursInWords[earningsTime] }}
        </button>
        <ul class="dropdown-menu" aria-labelledby="earnings-time">
          <li>
            <a
              class="dropdown-item"
              @click="earningsTime = 1"
              :class="earningsTime === 1 ? 'current' : ''"
            >
              Earning ~{{ Math.round(stakerEarningRateX1k * 1) / 1000 }}
              SVC / hour
            </a>
          </li>
          <li>
            <a
              class="dropdown-item"
              @click="earningsTime = 24"
              v-bind:class="earningsTime === 24 ? 'current' : ''"
            >
              Earning ~{{ Math.round(stakerEarningRateX1k * 24) / 1000 }}
              SVC / day
            </a>
          </li>
          <li>
            <a
              class="dropdown-item"
              @click="earningsTime = 168"
              v-bind:class="earningsTime === 168 ? 'current' : ''"
            >
              Earning ~{{ Math.round(stakerEarningRateX1k * 168) / 1000 }}
              SVC / week
            </a>
          </li>
          <li>
            <a
              class="dropdown-item"
              @click="earningsTime = 720"
              v-bind:class="earningsTime === 720 ? 'current' : ''"
            >
              Earning ~{{ Math.round(stakerEarningRateX1k * 720) / 1000 }}
              SVC / month
            </a>
          </li>
          <li>
            <a
              class="dropdown-item"
              @click="earningsTime = 8760"
              v-bind:class="earningsTime === 8760 ? 'current' : ''"
            >
              Earning ~{{ Math.round(stakerEarningRateX1k * 8760) / 1000 }}
              SVC / year
            </a>
          </li>
        </ul>
      </div>
      <div class="settings-menu">
        <button
          class="btn btn-primary"
          @click="harvest"
          :disabled="stakingPaused || womToHarvest == 0"
          style="font-size: 14px"
        >
          Harvest
          {{ Math.round(stakersPayoutPct * womToHarvest * 10) / 1000 }} SVC
        </button>
      </div>
    </div>
  </div>
</template>


<script>
import { ethers } from "ethers";
import Reaper from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";

// let RewardTokenAddress = window.RewardTokenAddress;

export default {
  name: "App",
  data() {
    return {
      rewardTokenContract: null,

      nftContract: null,
      stakingContract: null,

      provider: null,
      signer: null,

      womEmissionPerHour: null,

      stakersAddress: null,
      stakerStakedForSeconds: null,
      SECONDS_PER_DAY: 600, // TODO
      stakersPayoutPct: null,
      stakersNextPayoutPct: null,
      stakerNextPayoutPctInSeconds: null,

      stakersPower: null,
      totalStakedPower: null,
      baseStakingPower: null,
      stakerEarningRateX1k: null,

      earningsTime: 168,
      hoursInWords: {
        1: "hour",
        24: "day",
        168: "week",
        720: "month",
        8760: "year",
      },

      womToHarvest: null,
      stakingPaused: true,
      stakersRewardTokenBalance: 0,
    };
  },
  methods: {
    harvest: async function () {
      let tx = await this.stakingContract.harvest();
      await tx.wait();
      this.load();
    },
    approve: async function () {
      let tx = await this.nftContract.setApprovalForAll(
        window.ReaperAddress,
        true
      );
      await tx.wait();
      window.location.reload();
    },

    loadStakerInfo: function () {
      this.stakingContract
        .showStakerInfo(this.stakersAddress)
        .then((stakerInfo) => {
          let stakedForSeconds = stakerInfo[0];
          this.stakerStakedForSeconds = stakedForSeconds;
          if (stakedForSeconds > 30 * this.SECONDS_PER_DAY) {
            this.stakersPayoutPct = 115; // 15% bonus
            this.stakersNextPayoutPct = 115;
            this.stakerNextPayoutPctInSeconds = 0;
          } else if (stakedForSeconds < 19 * this.SECONDS_PER_DAY) {
            let stakedForDays = Math.floor(
              stakedForSeconds / this.SECONDS_PER_DAY
            );
            this.stakersPayoutPct = (stakedForDays + 1) * 5;
            this.stakersNextPayoutPct = this.stakersPayoutPct + 5;
            this.stakerNextPayoutPctInSeconds =
              (stakedForDays + 1) * this.SECONDS_PER_DAY - stakedForSeconds;
          } else {
            this.stakersPayoutPct = 100;
            this.stakersNextPayoutPct = 115;
            this.stakerNextPayoutPctInSeconds =
              30 * this.SECONDS_PER_DAY - stakedForSeconds;
          }

          this.stakersPower = stakerInfo[1];
          this.totalStakedPower = stakerInfo[2];
          this.baseStakingPower = stakerInfo[3];
          this.womEmissionPerHour =
            Math.round(ethers.utils.formatEther(stakerInfo[4]) * 100000) /
            100000;
          this.stakerEarningRateX1k =
            ((this.womEmissionPerHour * this.stakersPower) /
              (this.totalStakedPower - this.baseStakingPower)) *
            1000;
        });
      this.womToHarvest = 0;
      this.stakingContract
        .showStakersEarningsUntilLastTimestamp(this.stakersAddress)
        .then((amount) => {
          this.womToHarvest +=
            Math.round(ethers.utils.formatEther(amount) * 100000) / 100000;
        });
      this.stakingContract
        .showStakersEarningsAfterLastTimestamp(this.stakersAddress)
        .then((earnings) => {
          this.womToHarvest +=
            Math.round(ethers.utils.formatEther(earnings) * 100000) / 100000;
        });
      this.stakingContract.isMintPausedStakingPaused().then((result) => {
        this.stakingPaused = result[1];
      });
    },

    formatSeconds: function (seconds) {
      seconds = Number(seconds);
      var d = Math.floor(seconds / (3600 * 24));
      var h = Math.floor((seconds % (3600 * 24)) / 3600);
      var m = Math.floor((seconds % 3600) / 60);
      var s = Math.floor(seconds % 60);

      var dDisplay = d > 0 ? d + " d, " : "";
      var hDisplay = h > 0 ? h + " h, " : "";
      var mDisplay = m > 0 ? m + " min, " : "";
      var sDisplay = s > 0 ? s + " sec" : "";
      return dDisplay + hDisplay + mDisplay + sDisplay;
    },

    loadRewardTokenBalance: function () {
      console.log(this.stakersAddress);
      this.rewardTokenContract
        .balanceOf(this.stakersAddress)
        .then((balance) => {
          this.stakersRewardTokenBalance =
            Math.round(ethers.utils.formatEther(balance) * 100000) / 100000;
        });
    },

    load: function () {
      this.signer.getAddress().then((userAddress) => {
        this.stakersAddress = userAddress;
        this.loadStakerInfo();
        this.loadRewardTokenBalance();
      });
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();
        let _womContract = new ethers.Contract(
          window.RewardTokenAddress,
          RewardToken.abi,
          this.provider.getSigner()
        );
        let _stakingContract = new ethers.Contract(
          window.ReaperAddress,
          Reaper.abi,
          this.provider.getSigner()
        );

        this.rewardTokenContract = _womContract;
        this.stakingContract = _stakingContract;
      })
      .then(() => {
        this.load();
      });
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin-top: 60px;
}
</style>
