<template>
  <template v-if="stakersAddress === null">
    <div class="text-center">
      Please log into Metamask first, then refresh this website...
    </div>
  </template>

  <template
    v-if="
      loadingStakedNfts === true &&
      loadingUnstakedNfts === true &&
      stakersAddress !== null
    "
  >
    <div class="text-center">Loading NFTs...</div>
  </template>

  <div class="container">
    <div class="row">
      <div class="col-6">
        <h2 class="text-start">{{ projectname }}</h2>
        <div class="row">
          <div class="col-lg-4 col-sm-12 col-xs-12">
            <ul class="settings-menu">
              <button
                class="btn btn-primary"
                type="button"
                id="collection-filter"
                data-bs-toggle="dropdown"
                aria-powanded="false"
              >
                Filter
              </button>
              <ul class="dropdown-menu" aria-labelledby="collection-filter">
                <li>
                  <a
                    class="dropdown-item"
                    href="#"
                    @click="filterNfts('all')"
                    v-bind:class="filter === null ? 'current' : ''"
                    >All ({{ stakersNfts.length }})</a
                  >
                </li>
                <li>
                  <a
                    class="dropdown-item"
                    href="#"
                    @click="filterNfts('staked')"
                    v-bind:class="filter === 'staked' ? 'current' : ''"
                    >Staked ({{ stakedCount }})</a
                  >
                </li>
                <li>
                  <a
                    class="dropdown-item"
                    href="#"
                    @click="filterNfts('unstaked')"
                    v-bind:class="filter === 'unstaked' ? 'current' : ''"
                    >Unstaked ({{ unstakedCount }})</a
                  >
                </li>
              </ul>

              &nbsp;
              <button
                v-if="showLevelingExplanation && stakersTokenBalance > 0"
                @click="showLevelingExplanation = false"
                class="btn btn-primary"
              >
                Hide Explanation
              </button>
              <button
                v-if="!showLevelingExplanation && stakersTokenBalance > 0"
                @click="showLevelingExplanation = true"
                class="btn btn-primary"
              >
                Show Explanation
              </button>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <template v-if="transactionInProgress">
      <div class="container">
        <div class="row">
          <div class="col-12 text-center">
            Waiting for current transaction to finish...
          </div>
        </div>
      </div>
    </template>

    <div class="row text-center">
      <h4>{{ feedbackMessage }}</h4>
    </div>
    <br />

    <div v-if="stakersTokenAllowanceForLeveling <= 0" class="row">
      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <span
                  >Before you can upgrade your NFTs, you need to approve the SVC
                  token once.</span
                >
                <button
                  class="btn btn-primary"
                  type="button"
                  id="approveRewardToken"
                  @click="approveRewardToken"
                >
                  approve SVC
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div v-if="showLevelingExplanation || stakersTokenBalance <= 0" class="row">
      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <h4>NFT Level Upgrades</h4>
                <ul>
                  <li>
                    You can permanently increase the staking power of your NFTs
                    by upgrading the levels of any NFT you own.
                  </li>
                  <li>
                    For level upgrades you need SVC tokens which you can earn by
                    staking your NFTs or by buying them on MistSwap.
                  </li>
                  <li>
                    Each level upgrade costs <strong>100 SVC tokens. </strong>
                  </li>
                  <li>
                    The maximum level is <strong>{{ maxLevel }}. </strong>
                  </li>
                  <li>
                    Use the slider to select the level you want to upgrade to.
                    The website shows you a preview of the power after the
                    upgrade.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <h4>Automatic Harvests and Pure Harvests</h4>
                <ul>
                  <li>
                    Whenever you stake, unstake or upgrade one or multiple NFTs,
                    a harvest of SVC reward tokens is automatically executed.
                  </li>
                  <li>
                    Additionally, you can harvest SVC reward tokens whenever you
                    want. This operation is called a
                    <strong>pure harvest.</strong>
                  </li>
                </ul>
                <br />
                <h4>Harvest Penalties and Bonuses</h4>
                <h6>Penalties</h6>
                <ul>
                  <li>
                    If you harvest within the first 20 days of staking, the
                    amount is reduced with a penalty.
                  </li>
                  <li>
                    The penalty applies to both automatic and pure harvests.
                  </li>
                  <li>
                    Within the first day of staking, the penalty is 95% of the
                    harvest amount.
                  </li>
                  <li>
                    For each subsequent full day of staking, the penalty
                    percentage is reduced by 5%.
                  </li>
                  <li>
                    That means, you will receive 10% after a full day of
                    staking, 15% after 2 full days and so on.
                  </li>
                  <li>
                    After 19 full days of staking your NFTs, you will receive
                    100% of the harvest amount (no penalties).
                  </li>
                </ul>
                <br />
                <h6>Bonus</h6>
                <ul>
                  <li>
                    But wait, that's not all: After 30 full days of staking, you
                    will receive a 15% bonus. 👀
                  </li>
                  <li>
                    Note that each pure harvest and each unstake will reset the
                    penalties and bonuses cycle. A stake or level upgrade will
                    <strong>not</strong> reset the cycle.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div class="col-xxl-6">
        <div class="card top-bid">
          <div class="card-body">
            <div class="row align-items-center">
              <div class="col align-items-center">
                <h4>How Staking Your Reaperville NFTs Works</h4>
                <ul>
                  <li>
                    Normally, when you stake one or multiple NFTs, you transfer
                    the ownership of your NFTs to a smart contract which
                    promises to transfer them back when you unstake.
                  </li>
                  <li>
                    We at Surviville do things differently. When you stake a
                    Reaperville NFT, it remains in your wallet. However, you
                    cannot transfer a staked NFT to another wallet.
                  </li>
                  <li>
                    To be able to transfer a staked NFT, you have to unstake it
                    first -- which you can do at any time.
                  </li>
                </ul>
                <br />
                <h4>Advantages and Disadvantages</h4>
                <h6>Advantages</h6>
                <ul>
                  <li>
                    Super low gas fees for staking and unstaking your NFTs.
                  </li>
                  <li>
                    The staked NFTs remain in your wallet. No need to trust
                    another smart contract with the custody of your staked NFTs.
                  </li>
                </ul>
                <br />
                <h6>Disadvantages</h6>
                <ul>
                  <li>
                    Because the staked NFTs remain in your wallet, it may appear
                    as if you could transfer them. However, they can only be
                    transferred once you unstake them.
                  </li>
                  <li>
                    Note that third party DAPPs may not be aware of this
                    limitation. Attempted transfers of staked NFTs will fail
                    until you unstake the corresponding NFTs first with this
                    DAPP.
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <div class="col-12">
      <div class="card filter-tab">
        <div class="card-body bs-0 p-0 bg-transparent">
          <div class="row">
            <div class="col-xxl-12 col-xl-12 col-lg-12 col-md-12 col-sm-12">
              <div class="card-body">
                <h4 v-if="selectedNfts.length == 1">
                  1 NFT selected -- Balance:
                  {{ Math.round(stakersTokenBalance / 10 ** 15) / 1000 }} SVC
                  &nbsp;
                  <i
                    title="Refresh SVC Balance"
                    @click="refreshPowBalance"
                    class="ri-refresh-line"
                  ></i>
                </h4>
                <h4 v-else>
                  {{ selectedNfts.length }} NFTs selected -- Balance:
                  {{ Math.round(stakersTokenBalance / 10 ** 15) / 1000 }} SVC
                  <i
                    title="Refresh SVC Balance"
                    @click="refreshPowBalance"
                    class="ri-refresh-line"
                  ></i>
                </h4>
                <button
                  @click="selectVisible"
                  class="btn btn-success"
                  style="font-size: 14px"
                >
                  Select all
                </button>
                &nbsp;
                <button
                  @click="clearSelection"
                  class="btn btn-danger"
                  style="font-size: 14px"
                >
                  Clear selection
                </button>
                &nbsp;
                <button
                  @click="upgradeSelectedNfts()"
                  class="btn btn-success"
                  :disabled="
                    selectedNfts.length <= 0 ||
                    stakersTokenAllowanceForLeveling <= 0 ||
                    stakersTokenBalance <= 0 ||
                    stakingPaused
                  "
                  style="font-size: 14px"
                >
                  Upgrade {{ Math.min(selectedNfts.length, maxNftsPerTx) }} for
                  {{ tokenCostToBulkUpgrade }}
                  SVC
                </button>
                &nbsp;
                <button
                  @click="stakeSelected"
                  class="btn btn-success"
                  :disabled="selectedNfts.length <= 0 || stakingPaused"
                  style="font-size: 14px"
                >
                  Stake {{ Math.min(selectedNfts.length, maxNftsPerTx) }}
                </button>
                &nbsp;
                <button
                  @click="unstakeSelected"
                  class="btn btn-danger"
                  :disabled="selectedNfts.length <= 0 || stakingPaused"
                  style="font-size: 14px"
                >
                  Unstake {{ Math.min(selectedNfts.length, maxNftsPerTx) }}
                </button>
                <p v-if="selectedNfts.length > maxLevel">
                  Note: {{ maxLevel }} NFTs max staking and unstaking in 1
                  transaction
                </p>
              </div>
            </div>
          </div>

          <br />

          <div class="row">
            <template
              v-if="
                loadingStakedNfts === false &&
                loadingUnstakedNfts === false &&
                stakersNfts.length <= 0
              "
            >
              <h1 class="text-center">No NFTs to display</h1>
            </template>

            <div
              class="col-xxl-3 col-xl-4 col-lg-4 col-md-6 col-sm-6"
              v-for="tokenId in filteredNfts"
              :key="tokenId"
            >
              <div class="card items">
                <div class="card-body">
                  <div class="items-img position-relative">
                    <img
                      :src="nftInfo[tokenId].image"
                      class="img-fluid rounded mb-3"
                      alt=""
                      @click="selectNft(tokenId)"
                    />
                  </div>
                  <input
                    type="checkbox"
                    :value="tokenId"
                    v-model="selectedNfts"
                    @click="selectNft(tokenId)"
                  />
                  &nbsp;
                  <label>
                    <h4 class="card-title">
                      #{{ tokenId }}
                      &nbsp;
                      <span title="current level"
                        >🏆{{ nftInfo[tokenId].level }}</span
                      >
                      &nbsp;
                      <span title="current staking power"
                        >⚡{{ powerForLevel[nftInfo[tokenId].level] }}</span
                      >
                    </h4>
                  </label>
                  <div class="d-flex justify-content-center mt-3">
                    <button
                      v-if="nftInfo[tokenId].staked"
                      @click="unstake(tokenId)"
                      class="btn btn-danger"
                      :disabled="stakingPaused"
                    >
                      Unstake
                    </button>
                    <button
                      v-else
                      @click="stake(tokenId)"
                      class="btn btn-primary"
                      :disabled="stakingPaused"
                    >
                      Stake
                    </button>
                  </div>

                  <div v-if="nftInfo[tokenId].level >= maxLevel">
                    Max. level reached! 👏
                  </div>
                  <div v-else>
                    <div>
                      <span title="current level"
                        >🏆{{ nftInfo[tokenId].level }}</span
                      >
                      <input
                        type="range"
                        title="Choose desired level after upgrade"
                        class="form-control-range"
                        :min="nftInfo[tokenId].level + 1"
                        :max="maxLevel"
                        id="levelsToUpgradeSlider"
                        v-model="nftInfo[tokenId].nftLevelAfterUpgrade"
                        @change="updateLevelsToUpgrade(tokenId)"
                      />
                      <span
                        title="level after upgrade"
                        class="text-muted"
                        v-if="nftInfo[tokenId].nftLevelAfterUpgrade <= maxLevel"
                      >
                        ({{ nftInfo[tokenId].nftLevelAfterUpgrade }})
                      </span>
                      <span class="text-muted" title="power after upgrade">
                        (⚡{{
                          powerForLevel[nftInfo[tokenId].nftLevelAfterUpgrade]
                        }})</span
                      >
                    </div>
                    <button
                      @click="
                        upgradeSpecificNft(
                          tokenId,
                          nftInfo[tokenId].levelsToUpgrade
                        )
                      "
                      class="btn btn-primary"
                      :disabled="
                        stakersTokenBalance <= 0 ||
                        stakersTokenAllowanceForLeveling <= 0 ||
                        nftInfo[tokenId].level >= maxLevel ||
                        stakingPaused
                      "
                      style="font-size: 14px"
                    >
                      Upgrade
                      {{ nftInfo[tokenId].levelsToUpgrade }} level<template
                        v-if="nftInfo[tokenId].levelsToUpgrade != 1"
                        >s</template
                      >
                      for
                      {{ nftInfo[tokenId].levelsToUpgrade * tokenCostPerLevel }}
                      SVC
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const Web3 = require("web3");
import { ethers } from "ethers";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";

// TODO Testnet - Mainnet switch
// import reaperInitialStakes from "../reaper-initial-stakes.json";
// import reaperLevel from "../reaper-level.json";

// TODO Testnet - Mainnet switch
import reaperInitialStakesTest from "../reaperTest-initial-stakes.json";

import reaperTestLevel from "../reaperTest-level.json";

// TODO Testnet - Mainnet switch
// let reaper = {
//   levels: reaperLevel,
//   initialStakes: reaperInitialStakes,
//   pictureLink:
//     "https://raw.githubusercontent.com/WorldOfMasks/data/main/json/",
// };

// TODO Testnet - Mainnet switch
let reaperTest = {
  levels: reaperTestLevel,
  initialStakes: reaperInitialStakesTest,
  pictureLink:
    "https://raw.githubusercontent.com/Surviville/Reaperville/master/png/",
};

const nftCollection = {
  // TODO Testnet - Mainnet switch
  // "0xf928B82061bc531e3B24B8CF3736724e35988b7a": reaper,

  // TODO Testnet - Mainnet switch
  "0x4C40680ab319411a0F98Fb5DBc7105E64da12B18": reaperTest,
};

function removeItemOnce(arr, value) {
  let index = arr.indexOf(value);
  if (index > -1) {
    arr.splice(index, 1); // 2nd parameter: Amount of elements to remove
  } else {
    console.log("Unstaking an NFT that has not been staked before!");
  }
  return arr;
}

// function matchesAllFilters(nft, filters) {
//   for (const [category, traits] of Object.entries(filters)) {
//     if (0 === traits.length) {
//       // console.log("No filter on " + category + " specified");
//       continue;
//     } else if (-1 === traits.indexOf(nft["traits"][category])) {
//       // console.log("Specified traits for " + category + " not in nft " + nft.id +". Trait is " + nft["traits"][category]);
//       return false;
//     }
//   }
//   // console.log(nft.id + " matches all filters");
//   return true;
// }

export default {
  name: "App",
  props: ["nftCA", "projectname"],
  emits: ["updateStakingPowerEvent"],

  data() {
    return {
      rpc: null,

      maxLevel: 100,
      tokenCostPerLevel: 100,
      tokenCostToBulkUpgrade: 0,

      powerForLevel: [
        1000, 1050, 1104, 1162, 1224, 1291, 1363, 1440, 1523, 1613, 1710, 1815,
        1928, 2051, 2184, 2329, 2486, 2657, 2844, 3048, 3271, 3515, 3782, 4075,
        4397, 4730, 5074, 5428, 5792, 6165, 6546, 6934, 7329, 7730, 8136, 8546,
        8959, 9375, 9793, 10212, 10632, 11051, 11469, 11886, 12301, 12713,
        13122, 13527, 13928, 14324, 14715, 15101, 15481, 15855, 16223, 16584,
        16939, 17287, 17628, 17962, 18288, 18607, 18918, 19222, 19519, 19808,
        20090, 20364, 20631, 20891, 21143, 21391, 21634, 21873, 22107, 22336,
        22561, 22781, 22997, 23210, 23421, 23630, 23836, 24040, 24242, 24441,
        24638, 24833, 25025, 25215, 25402, 25587, 25770, 25950, 26128, 26304,
        26477, 26648, 26817, 26983, 27147,
      ],

      maxNftsPerTx: 50,

      showLevelingExplanation: false,

      stakersAddress: null,

      currentCollection: null,

      stakersTokenBalance: 0,
      stakersTokenAllowanceForLeveling: 0,

      feedbackMessage: "",

      nftContract: null,
      stakingContract: null,
      rewardTokenContract: null,

      provider: null,
      signer: null,

      loadingUnstakedNfts: true,
      loadingStakedNfts: true,

      stakersNfts: [],
      filteredNfts: [],
      filter: "all",
      selectedNfts: [],
      nftInfo: {},

      stakedCount: 0,
      unstakedCount: 0,

      picLink: "",

      nftLevel: {},

      transactionInProgress: false,

      stakingPaused: true,
    };
  },

  methods: {
    // updateFilters: function () {
    //   that.filters = {}
    //   filters.Background = getSelectValues(document.getElementById("Background"));
    //   filters.Fur = getSelectValues(document.getElementById("Fur"));
    //   filters.Clothes = getSelectValues(document.getElementById("Clothes"));
    //   filters.Eyes = getSelectValues(document.getElementById("Eyes"));
    //   filters.Mouth = getSelectValues(document.getElementById("Mouth"));
    //   filters.Earring = getSelectValues(document.getElementById("Earring"));
    //   filters.Hat = getSelectValues(document.getElementById("Hat"));
    //   filters.Profile = getSelectValues(document.getElementById("Profile"));
    //   filters.Special = getSelectValues(document.getElementById("Special"));
    //   filters.trait_count = getSelectValues(document.getElementById("trait_count"));

    //   for (let nft of that.sts) {
    //     if (false == matchesAllFilters(nft, filters)) continue;
    //   }
    // },

    refreshPowBalance: function () {
      this.feedbackMessage = "Updating SVC Balance";
      this.stakersTokenBalance = "updating";
      this.rewardTokenContract
        .balanceOf(this.stakersAddress)
        .then((balance) => {
          this.stakersTokenBalance = balance;
        });
      this.feedbackMessage = "Reward Token Balance Updated";
    },

    selectNft: function (_tokenId) {
      let that = this;
      if (that.selectedNfts.indexOf(_tokenId) < 0) {
        that.selectedNfts.push(_tokenId);
      } else {
        removeItemOnce(that.selectedNfts, _tokenId);
      }
      that.updateCostForBulkUpgrade();
    },

    updateLevelsToUpgrade: function (_tokenId) {
      let that = this;
      if (that.selectedNfts.indexOf(_tokenId) < 0) {
        that.selectedNfts.push(_tokenId);
      }
      if (that.nftInfo[_tokenId].nftLevelAfterUpgrade > that.maxLevel) {
        that.nftInfo[_tokenId].nftLevelAfterUpgrade = that.maxLevel;
      }
      that.nftInfo[_tokenId].levelsToUpgrade =
        that.nftInfo[_tokenId].nftLevelAfterUpgrade -
        that.nftInfo[_tokenId].level;

      that.updateCostForBulkUpgrade();
    },

    getLevel: function (_tokenId) {
      let level = this.currentCollection.levels["level"][_tokenId];
      if (!level) {
        // set initial level
        if (_tokenId < 10) {
          level = 10;
        } else if (_tokenId == 2500) {
          level = 0;
        } else {
          level = 9 - Math.floor(_tokenId / 250);
        }
      }
      return level;
    },

    getPower: function (_tokenId) {
      return this.powerForLevel[this.getLevel(_tokenId)];
    },

    approveRewardToken: async function () {
      let that = this;

      that.feedbackMessage = "Approving SVC token";

      let tx = await that.rewardTokenContract.approve(
        window.ReaperAddress,
        ethers.constants.MaxUint256,
        {
          gasLimit: 65000, // 60k is probably fine, too
          gasPrice: 1046740000,
        }
      );
      await tx.wait();
      that.rewardTokenContract
        .allowance(that.stakersAddress, window.ReaperAddress)
        .then((allowance) => {
          that.stakersTokenAllowanceForLeveling = allowance;
        });
      that.feedbackMessage = "";
    },

    stake: async function (tokenId) {
      this.transactionInProgress = true;
      this.feedbackMessage = "";
      let tx = await this.stakingContract.stake([tokenId], {
        gasLimit: 230000,
        gasPrice: 1046740000,
      });
      // let tx = await this.stakingContract.stake([tokenId]);
      await tx.wait();
      this.load();
    },

    unstake: async function (tokenId) {
      this.transactionInProgress = true;
      this.feedbackMessage = "";
      let tx = await this.stakingContract.unstake([tokenId], {
        gasLimit: 290000,
        gasPrice: 1046740000,
      });
      await tx.wait();
      this.load();
    },

    selectVisible: function () {
      this.feedbackMessage = "";
      this.selectedNfts = [];
      for (let tokenId of this.filteredNfts) {
        if (this.selectedNfts.indexOf(tokenId) < 0) {
          this.selectedNfts.push(tokenId);
        }
      }

      this.updateCostForBulkUpgrade();
    },

    clearSelection: function () {
      this.feedbackMessage = "";
      this.selectedNfts = [];
      this.updateCostForBulkUpgrade();
    },

    stakeSelected: async function () {
      this.transactionInProgress = true;
      this.feedbackMessage = "";

      let IDs = [];

      for (let tokenId of this.selectedNfts) {
        if (
          this.nftInfo[tokenId].staked === false &&
          this.filteredNfts.indexOf(tokenId) > -1 &&
          IDs.length < this.maxNftsPerTx
        ) {
          // console.log(this.selectedNfts[i], this.stakersNfts[findIdx]);
          IDs.push(tokenId);
        }
      }

      this.selectedNfts = IDs.slice();

      if (IDs.length === 0) {
        this.feedbackMessage = "No selected NFT matches all criteria.";
        return false;
      } else {
        this.feedbackMessage = "Staking selected NFTs.";
      }

      let tx = await this.stakingContract.stake(IDs, {
        gasLimit: Math.min(50000 + 130000 * IDs.length, 9999999),
        gasPrice: 1046740000,
      });
      await tx.wait();
      this.load();
    },

    unstakeSelected: async function () {
      this.transactionInProgress = true;
      this.feedbackMessage = "";

      let IDs = [];

      for (let tokenId of this.selectedNfts) {
        if (
          this.nftInfo[tokenId].staked === true &&
          this.filteredNfts.indexOf(tokenId) > -1 &&
          IDs.length < this.maxNftsPerTx
        ) {
          // console.log(this.selectedNfts[i], this.filteredNfts[findIdx]);
          IDs.push(tokenId);
        }
      }

      this.selectedNfts = IDs.slice();

      if (IDs.length === 0) {
        this.feedbackMessage = "No selected NFT matches all criteria.";
        return false;
      } else {
        this.feedbackMessage = "Unstaking selected NFTs.";
      }

      let tx = await this.stakingContract.unstake(IDs, {
        gasLimit: Math.min(125000 + 200000 * IDs.length, 9999999),
        gasPrice: 1046740000,
      });
      await tx.wait();
      this.load();
    },

    updateCostForBulkUpgrade: function () {
      let nftsFound = 0;
      this.tokenCostToBulkUpgrade = 0;

      for (let idx = 0; idx < this.selectedNfts.length; idx++) {
        let tokenId = this.selectedNfts[idx];
        let cost =
          this.nftInfo[tokenId].levelsToUpgrade * this.tokenCostPerLevel;
        if (nftsFound >= this.maxNftsPerTx) {
          this.feedbackMessage = "Max. number of NFTs selected";
          removeItemOnce(this.selectedNfts, tokenId);
          idx--;
        } else {
          this.tokenCostToBulkUpgrade += cost;
          nftsFound++;
        }
      }
    },

    upgradeSpecificNft: async function (_tokenId, _levelsToUpgrade) {
      if (_levelsToUpgrade === 0) {
        this.feedbackMessage = "Can't upgrade 0 levels";
        return false;
      }

      this.feedbackMessage = "";

      let that = this;
      let _rewardTokens = _levelsToUpgrade * that.tokenCostPerLevel;

      // checking SVC token
      if (_rewardTokens > that.stakersTokenAllowanceForLeveling) {
        that.feedbackMessage = "Please increase your SVC token allowance first";
        return false;
      } else if (_rewardTokens > that.stakersTokenBalance) {
        that.feedbackMessage = "SVC token balance is too low";
        return false;
      }

      let level = that.getLevel(_tokenId);
      if (level >= that.maxLevel) {
        that.feedbackMessage =
          "Cannot upgrade " + _tokenId + ", because it's already at max. level";
        console.log(that.feedbackMessage);
        return false;
      }

      // check owner
      let _nftOwner = await that.nftContract.ownerOf(_tokenId);
      if (_nftOwner !== that.stakersAddress) {
        if (_nftOwner !== window.ReaperAddress) {
          that.feedbackMessage =
            "Cannot upgrade " + _tokenId + ", because it's not in your wallet";
          console.log(that.feedbackMessage);
          return false;
        }
      }

      that.feedbackMessage =
        "Upgrading 1 NFT by " +
        _levelsToUpgrade +
        " levels with " +
        _rewardTokens +
        " SVC";
      console.log(that.feedbackMessage);

      console.log("Power old= " + that.getPower(_tokenId));
      console.log("Level old= " + level);
      console.log("Balance old= " + that.stakersTokenBalance);
      let balanceNew = that.stakersTokenBalance - _rewardTokens;
      console.log("Balance new= " + balanceNew);

      let tx = await that.stakingContract.upgradeLevel(
        [_tokenId],
        [_levelsToUpgrade],
        {
          gasLimit: 180000,
          gasPrice: 1046740000,
        }
      );
      await tx.wait();

      if (that.nftInfo[_tokenId].staked) {
        // that.load();
        // that.$emit('updateStakingPowerEvent');
        window.location.reload();
      } else {
        that.load();
      }
    },

    upgradeSelectedNfts: async function () {
      this.feedbackMessage = "";

      let that = this;

      // checking SVC token
      if (that.tokenCostToBulkUpgrade > that.stakersTokenAllowanceForLeveling) {
        that.feedbackMessage = "Please increase your SVC token allowance first";
        return false;
      } else if (that.tokenCostToBulkUpgrade > that.stakersTokenBalance) {
        that.feedbackMessage = "SVC token balance is too low";
        return false;
      }

      if (that.selectedNfts.length < 1) {
        that.feedbackMessage = "No NFTs selected";
        return false;
      } else if (that.selectedNfts.length == 1) {
        let tokenId = that.selectedNfts[0];
        let level = that.getLevel(tokenId);
        if (level >= that.maxLevel) {
          that.feedbackMessage =
            "Cannot upgrade " +
            tokenId +
            ", because it's already at max. level";
          console.log(that.feedbackMessage);
          return false;
        }

        // check owner
        let _nftOwner = await this.nftContract.ownerOf(tokenId);
        if (_nftOwner !== that.stakersAddress) {
          if (_nftOwner !== window.ReaperAddress) {
            that.feedbackMessage = "You don't own " + tokenId;
            console.log(that.feedbackMessage);
            return false;
          }
        }

        that.feedbackMessage =
          "Upgrading 1 NFT with " +
          that.nftInfo[tokenId].levelsToUpgrade +
          " SVC";
        console.log(that.feedbackMessage);

        console.log("Level old= " + level);
        console.log("Balance old= " + that.stakersTokenBalance);
        let balanceNew =
          that.stakersTokenBalance - that.nftInfo[tokenId].levelsToUpgrade;
        console.log("Balance new= " + balanceNew);

        let tx = await that.stakingContract.upgradeLevel(
          [tokenId],
          [that.nftInfo[tokenId].levelsToUpgrade],
          {
            gasLimit: 180000,
            gasPrice: 1046740000,
          }
        );
        await tx.wait();
        if (that.nftInfo[tokenId].staked) {
          // that.emit('updateStakingPowerEvent');
          window.location.reload();
        } else {
          that.load();
        }
      } else if (that.selectedNfts.length > 1) {
        // go through selected NFTs and pick first maxNftsPerTx which qualify for an upgrade
        let nftsToUpgradeArray = [];
        let levelsToUpgradeArray = [];
        that.feedbackMessage =
          "Double checking ownership and level of selected NFTs";
        for (
          let i = 0;
          i < that.selectedNfts.length &&
          nftsToUpgradeArray.length < that.maxNftsPerTx;
          i++
        ) {
          let tokenId = that.selectedNfts[i];
          // check cost
          if (that.nftInfo[tokenId].levelsToUpgrade === 0) {
            that.feedbackMessage +=
              " -- Skipping " + tokenId + " (0 SVC upgrade)";
            continue;
          }

          // check level
          if (that.getLevel(tokenId) >= that.maxLevel) {
            that.feedbackMessage +=
              " -- Skipping " + tokenId + " (already at max level)";
            continue;
          }

          // check owner
          let _nftOwner = await this.nftContract.ownerOf(tokenId);
          if (_nftOwner !== that.stakersAddress) {
            if (_nftOwner !== window.ReaperAddress) {
              that.feedbackMessage +=
                " -- Skipping " + tokenId + " (not in your wallet)";
              console.log(that.feedbackMessage);
            }
          }

          nftsToUpgradeArray.push(that.selectedNfts[i]);
          levelsToUpgradeArray.push(that.nftInfo[tokenId].levelsToUpgrade);
          console.log("Adding " + tokenId);
          console.log(nftsToUpgradeArray);
        }

        that.feedbackMessage +=
          " -- " + nftsToUpgradeArray.length + " NFTs passed all checks";
        console.log(nftsToUpgradeArray);
        console.log(levelsToUpgradeArray);
        console.log("Bulk Upgrade Cost= " + that.tokenCostToBulkUpgrade);

        console.log("Balance old=" + that.stakersTokenBalance);
        let powBalanceAfterUpgrade =
          that.stakersTokenBalance - that.tokenCostToBulkUpgrade;
        console.log("Balance after bulk upgrade: ", powBalanceAfterUpgrade);

        if (nftsToUpgradeArray.length <= 0) {
          return false;
        }

        that.feedbackMessage +=
          " -- Upgrading NFTs [" +
          nftsToUpgradeArray +
          "] with [" +
          levelsToUpgradeArray +
          "] SVC";
        let tx = await this.stakingContract.upgradeLevel(
          nftsToUpgradeArray,
          levelsToUpgradeArray,
          {
            gasLimit: Math.min(
              20000 + 160000 * nftsToUpgradeArray.length,
              9999999
            ),
            gasPrice: 1046740000,
          }
        );
        await tx.wait();
      }

      window.location.reload();
    },

    // upgrade1NftGasSavings: function (_rewardTokens, _tokenId, startPwrX10k, startLvl) {
    //   console.log("V2+ (gas savings) start");

    //   let that = this;
    //   let rewardTokensConsumed = 0;

    // console.log("after full + partial level upgrades: level: " + newLvl + " power: " + newPwrX10k + " -- tokenCostPerLevel: " + tokenCostPerLevel + " -- minpower: ", this.getMinPower(newLvl), this.getMinPower(newLvl + 1));

    // return { rewardTokensConsumed, newPwrX10k, newLvl };
    // },

    calculatePowerIncreaseSolidityV2: function (_rewardTokens, _tokenId) {
      let startLvl = this.nftLevel[_tokenId - 1];
      let startPwrX10k = this.getPower(_tokenId);

      let result2 = this.upgrade1NftGasSavings(
        _rewardTokens,
        _tokenId,
        startPwrX10k,
        startLvl
      );

      this.nftInfo[_tokenId].nftPowerAfterUpgrade = result2.newPwrX10k;
      this.nftInfo[_tokenId].nftLevelAfterUpgrade = result2.newLvl;
      this.nftInfo[_tokenId].levelsToUpgrade = result2.rewardTokensConsumed;
      let minpwrLevel = this.getMinPower(result2.newLvl);
      let minpwrNextLevel = this.getMinPower(result2.newLvl + 1);
      this.nftInfo[_tokenId].pctInLevelX100AfterUpgrade = Math.min(
        10000,
        Math.round(
          (((result2.newPwrX10k - minpwrLevel) /
            (minpwrNextLevel - minpwrLevel)) *
            10000) /
            0.7
        )
      );
    },

    filterNfts: function (_param) {
      let that = this;
      let staked = [];
      let unstaked = [];
      that.filter = _param;
      switch (_param) {
        case "all":
          // that.filteredNfts = that.stakersNfts;
          staked = that.stakersNfts.filter((tokenId) => {
            return that.nftInfo[tokenId].staked === true;
          });
          unstaked = that.stakersNfts.filter((tokenId) => {
            return that.nftInfo[tokenId].staked === false;
          });
          that.filteredNfts = staked.concat(unstaked);
          break;
        case "staked":
          that.filteredNfts = that.stakersNfts.filter((tokenId) => {
            return that.nftInfo[tokenId].staked === true;
          });
          break;
        case "unstaked":
          that.filteredNfts = that.stakersNfts.filter((tokenId) => {
            return that.nftInfo[tokenId].staked === false;
          });
          break;
        default:
          that.feedbackMessage = "Filter not implemented";
      }
    },

    fillNftInfo: function (_inArr, _staked) {
      let that = this;
      for (let i in _inArr) {
        let tokenId = _inArr[i];
        that.stakersNfts.push(tokenId);
        let level = that.getLevel(tokenId);

        that.nftInfo[tokenId] = {
          staked: _staked,
          level: level,
          nftLevelAfterUpgrade: level + 1,
          levelsToUpgrade: 1,
          image: that.currentCollection.pictureLink + tokenId + ".png",
        };
      }
      if (
        that.loadingStakedNfts === false &&
        that.loadingUnstakedNfts === false
      ) {
        that.stakersNfts.sort(function (a, b) {
          return that.nftInfo[b].level - that.nftInfo[a].level;
        });
        console.log("calling filter");
        that.filterNfts(that.filter);
        this.transactionInProgress = false;
      }
    },

    updateLevels: async function () {
      this.feedbackMessage = "";

      let that = this;

      return new Promise((resolve, reject) => {
        // loading power updates that came later
        console.log(
          "nextBlock: " + that.currentCollection.levels.nextBlockToCheck
        );
        const optionsPower = {
          fromBlock: that.currentCollection.levels.nextBlockToCheck, //Number || "earliest" || "pending" || "latest"
          toBlock: "latest",
        };

        try {
          let web3HttpReadLevels = new Web3(this.rpc);
          const stakeContractHttp = new web3HttpReadLevels.eth.Contract(
            Reaperville.abi,
            window.ReaperAddress
          );

          console.log("Getting past events for power upgrades");
          stakeContractHttp
            .getPastEvents("levelUpgraded", optionsPower)
            .then(function (events) {
              if (0 >= events.length) {
                console.log("No events");
                return true;
              }
              // that.currentCollection.level.nextBlockToCheck = Math.max(that.currentCollection.level.nextBlockToCheck, (events[events.length - 1].blockNumber + 1));

              console.log(events.length + " events found for power upgrades");

              for (let item of events) {
                // console.log(item);
                let id = parseInt(item.returnValues.tokenId);
                let newLevel = parseInt(item.returnValues.newLevel);

                that.currentCollection.levels["level"][id] = newLevel;
                console.log(
                  id +
                    " upgraded to " +
                    newLevel +
                    " == " +
                    that.currentCollection.levels["level"][id] +
                    " in " +
                    item.blockNumber
                );
              }
            })
            .then(() => {
              console.log("returning Power");
              resolve();
            });
        } catch (e) {
          console.log(e);
          reject();
        }
      });
    },

    load: async function () {
      let that = this;
      that.loadingUnstakedNfts = true;
      that.loadingStakedNfts = true;

      that.filteredNfts = [];
      that.stakersNfts = [];

      that.currentCollection = nftCollection[that.nftCA];

      that.stakersAddress = await that.signer.getAddress();

      that.stakingContract.isMintPausedStakingPaused().then((result) => {
        console.log(result);
        that.stakingPaused = result[1];
      });

      // load SVC balance and allowance
      that.rewardTokenContract
        .balanceOf(that.stakersAddress)
        .then((balance) => {
          that.stakersTokenBalance = balance;
        });
      that.rewardTokenContract
        .allowance(that.stakersAddress, window.ReaperAddress)
        .then((allowance) => {
          that.stakersTokenAllowanceForLeveling = allowance;
        });

      //////////////////
      // Update power //
      //////////////////
      await that.updateLevels(); // updates this.nftPower
      // console.log("nftPower in load: " + that.nftPower);

      ////////////
      // staked //
      ////////////
      that.nftContract
        .walletOfOwnerStaked(that.stakersAddress)
        .then((tokens) => {
          let tokensStaked = [];
          for (let iCopy = 0; iCopy < tokens.length; iCopy++) {
            let tokenId = Number(tokens[iCopy]);
            if (tokenId === 0) {
              continue;
            }
            tokensStaked.push(tokenId);
          }
          that.loadingStakedNfts = false;
          that.stakedCount = tokensStaked.length;
          that.fillNftInfo(tokensStaked, true);
        });

      //////////////
      // unstaked //
      //////////////
      that.nftContract.walletOfOwner(that.stakersAddress).then((tokens) => {
        let tokensInWallet = [];
        for (let iCopy = 0; iCopy < tokens.length; iCopy++) {
          let tokenId = Number(tokens[iCopy]);
          if (tokenId === 0) {
            continue;
          }
          tokensInWallet.push(tokenId);
        }
        that.loadingUnstakedNfts = false;
        that.unstakedCount = tokensInWallet.length;
        that.fillNftInfo(tokensInWallet, false);
      });
    },
  },

  mounted: function () {
    this.rpc = window.ethereum; // doesn't work on Android due to a bug(?)

    this.provider = new ethers.providers.Web3Provider(this.rpc, "any");

    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();

        let _nftContract = new ethers.Contract(
          this.nftCA.toString(),
          Reaperville.abi,
          this.provider.getSigner()
        );
        this.stakingContract = new ethers.Contract(
          window.ReaperAddress,
          Reaperville.abi,
          this.provider.getSigner()
        );

        let _rewardTokenContract = new ethers.Contract(
          window.RewardTokenAddress,
          RewardToken.abi,
          this.provider.getSigner()
        );

        this.nftContract = _nftContract;
        this.rewardTokenContract = _rewardTokenContract;
      })
      .then(() => {
        this.load();
      });
  },
};
</script>

