<template>
  <div class="content-body">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <h2>NFT Minting</h2>
          <template v-if="mintPaused"
            >The minting is currently paused. Please check back later!</template
          >
          <template v-else>
            <p>
              Mint some Reaperville NFTs on the testnet for
              {{ Math.round(reaperBchPerNft/10**12)/10**6 }} testnet BCH!
            </p>
            <button @click="mintReaper(40)" class="btn btn-primary">
              Mint 40
            </button>
            &nbsp;
            <button @click="mintReaper(25)" class="btn btn-primary">
              Mint 25
            </button>
            &nbsp;
            <button @click="mintReaper(5)" class="btn btn-primary">
              Mint 5
            </button>
            <br />
            <br />
            <br />
            {{ feedbackMessage }}
            <br />
            <br />
            <br />
          </template>

          <p>
            Your current BCH balance:
            {{ Math.round(stakersBchBalance / 10 ** 15) / 10 ** 3 }} BCH
          </p>
          <br />

          <h1>---TESTNET ONLY---</h1>

          <h2>Free Testnet BCH</h2>
          <p>
            If you need testnet BCH, please claim it for free at:
            <a href="http://54.169.31.93:8080/faucet" target="_blank"
              >http://54.169.31.93:8080/faucet</a
            >
          </p>
          <br />

          <div class="col-xxl-12">
            <h2>For test purposes, you can burn Surviville Coins here:</h2>
            <p>
              Your SVC token balance:
              {{ Math.round(stakersTokenBalance / 10 ** 15) / 10 ** 3 }} SVC
            </p>
            <button @click="burnRewardToken(0)" class="btn btn-primary">
              Burn My SVC
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ethers } from "ethers";
import Reaperville from "../../artifacts/contracts/Reaperville.sol/Reaperville.json";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";

export default {
  name: "App",
  components: {},
  data() {
    return {
      provider: null,
      signer: null,

      stakersAddress: "",
      feedbackMessage: "",

      reaperContract: null,
      rewardTokenContract: null,

      reaperBchPerNft: 20000000000000000,
      mintPaused: true,

      stakersBchBalance: 0,
      stakersTokenBalance: 0,
    };
  },
  methods: {
    burnRewardToken: async function (amount) {
      this.feedbackMessage = "";
      if (amount == 0) {
        amount = this.stakersTokenBalance;
      }
      if (amount == 0) {
        this.feedbackMessage = "Nothing to burn!";
        return false;
      }
      let tx = await this.rewardTokenContract.burn(amount);
      await tx.wait();
      this.load();
    },

    getRewardTokenBalance: function () {
      this.rewardTokenContract
        .balanceOf(this.stakersAddress)
        .then((balance) => {
          this.stakersTokenBalance = balance;
        });
    },

    getBchBalance: function () {
      this.provider.getBalance(this.stakersAddress).then((balance) => {
        this.stakersBchBalance = balance;
      });
    },

    mintReaper: async function (amount) {
      let bchAmount = this.reaperBchPerNft * amount;
      if (bchAmount > this.stakersBchBalance * 10 ** 15) {
        this.feedbackMessage = "Not enough BCH!";
        return false;
      }
      this.feedbackMessage = "Minting NFTs. Please wait!";
      let c = new ethers.Contract(
        window.ReaperAddress,
        Reaperville.abi,
        this.provider.getSigner()
      );
      let tx = await c.mint(amount, {
        value: bchAmount.toString(), 
        gasLimit: 9999999,
        gasPrice: 1046740000,
      });
      await tx.wait();
      window.location.reload();
    },

    load: async function () {
      this.signer.getAddress().then((myAddress) => {
        this.stakersAddress = myAddress;
        this.getBchBalance();
        this.getRewardTokenBalance();
      });
      this.reaperContract.isMintPausedStakingPaused().then((result) => {
        this.mintPaused = result[0];
      });
      this.reaperContract.bchPerNft().then((_bchPerNft) => {
        this.reaperBchPerNft = _bchPerNft;
      });
    },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, "any");
    this.provider
      .send("eth_requestAccounts", [])
      .then(() => {
        this.signer = this.provider.getSigner();

        this.reaperContract = new ethers.Contract(
          window.ReaperAddress,
          Reaperville.abi,
          this.provider.getSigner()
        );

        let _womContract = new ethers.Contract(
          window.RewardTokenAddress,
          RewardToken.abi,
          this.provider.getSigner()
        );

        this.rewardTokenContract = _womContract;
      })
      .then(() => {
        this.load();
      });
  },
};
</script>